// app/(auth)/login/page.tsx
"use client";
import React from "react";
import { useForm } from "react-hook-form";
import { useAuth } from "../../../components/AuthProvider";
import { useRouter } from "next/navigation";

type FormData = { email: string; password: string };

export default function LoginPage() {
  const { register, handleSubmit } = useForm<FormData>();
  const { login } = useAuth();
  const router = useRouter();

  const onSubmit = async (data: FormData) => {
    try {
      await login(data.email, data.password);
      router.push("/todos");
    } catch (err: any) {
      alert(err?.message || "Login failed");
    }
  };

  return (
    <div className="min-h-screen flex items-center justify-center bg-slate-50">
      <form onSubmit={handleSubmit(onSubmit)} className="bg-white p-8 rounded-lg shadow-md w-full max-w-md">
        <h2 className="text-2xl font-semibold mb-4">Login</h2>
        <label className="block mb-2">Email
          <input {...register("email")} type="email" required className="w-full mt-1 p-2 border rounded" />
        </label>
        <label className="block mb-4">Password
          <input {...register("password")} type="password" required className="w-full mt-1 p-2 border rounded" />
        </label>
        <button type="submit" className="w-full py-2 rounded bg-primary text-white mb-4">Sign in</button>
        <p className="text-center text-sm text-gray-600">
          Don't have an account?{" "}
          <a href="/signup" className="text-primary hover:underline font-medium">
            Sign up
          </a>
        </p>
      </form>
    </div>
  );
}
